@import "../../bower_components/sass-material-colors/sass/_sass-material-colors.scss";
@import "utils/animtion-tool";

.lfx-square-item {
    height: 60px;
    width: 120px;
    background-color: material-color("blue");
    margin: 0 auto;
    border-radius: 10px;
    opacity: 0;
}

.lfx-ani-delay-3 {
    animation-delay: 3s;
}



@include create-lfx-ani-delay-class(300);

@include create-lfx-ani-delay-class(400);

@include create-lfx-ani-delay-class(500);

@include create-lfx-ani-delay-class(600);

@include create-lfx-ani-delay-class(750);

@include create-lfx-ani-duration-class(500);
