@mixin create-lfx-ani-delay-class($delaytime, $unit: ms, $prefix: ".lfx-ani-delay") {
    #{$prefix}-#{$delaytime}-#{$unit} {
        animation-delay: $delaytime + $unit;
    }
}

@mixin create-custom-delay-class($delaytime, $unit: ms) {
    @include create-lfx-ani-delay-class($delaytime, $unit, $prefix: ".custom");
}

@mixin create-lfx-ani-duration-class($delaytime, $unit: ms, $prefix: ".lfx-ani-duration") {
    #{$prefix}-#{$delaytime}-#{$unit} {
        animation-duration: $delaytime + $unit;
    }
}

@mixin create-lfx-ani-iteration-count-class($count: 1, $prefix: ".lfx-ani-iteration-count") {
    #{$prefix}-#{$count} {
        animation-iteration-count: $count;
        @content;
    }
}